
"use client";

import Image from "next/image";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuLabel, DropdownMenuTrigger } from "@/components/ui/dropdown-menu";
import { MoreHorizontal, PlusCircle } from "lucide-react";
import { products as initialProducts } from "@/lib/data";
import { useState } from "react";
import { AddProductDialog } from "@/components/add-product-dialog";
import type { Product } from "@/lib/types";

export default function ProductsPage() {
  const [products, setProducts] = useState<Product[]>(initialProducts);
  const [isAddProductDialogOpen, setIsAddProductDialogOpen] = useState(false);

  const handleAddProduct = (newProduct: Omit<Product, 'id' | 'availability' | 'imageUrl'>) => {
    const productToAdd: Product = {
      ...newProduct,
      id: `prod-${Math.random().toString(36).substr(2, 9)}`,
      availability: newProduct.stock > 0 ? 'Available' : 'Rented',
      imageUrl: 'https://placehold.co/600x400.png',
    };
    setProducts(prevProducts => [...prevProducts, productToAdd]);
    // Also update the shared data source if other components need it
    // This is a temporary solution for the prototype.
    initialProducts.push(productToAdd);
  };

  return (
    <>
      <AddProductDialog 
        open={isAddProductDialogOpen} 
        onOpenChange={setIsAddProductDialogOpen}
        onAddProduct={handleAddProduct}
      />
      <Card>
        <CardHeader className="flex flex-row items-center justify-between">
          <CardTitle>Rental Products</CardTitle>
          <Button size="sm" className="gap-1" onClick={() => setIsAddProductDialogOpen(true)}>
            <PlusCircle className="h-4 w-4" />
            Add Product
          </Button>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead className="hidden w-[100px] sm:table-cell">Image</TableHead>
                <TableHead>Name</TableHead>
                <TableHead>Category</TableHead>
                <TableHead>Stock</TableHead>
                <TableHead>Status</TableHead>
                <TableHead className="text-right">Daily Rate</TableHead>
                <TableHead>
                  <span className="sr-only">Actions</span>
                </TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {products.map((product) => (
                <TableRow key={product.id}>
                  <TableCell className="hidden sm:table-cell">
                    <Image
                      alt="Product image"
                      className="aspect-square rounded-md object-cover"
                      data-ai-hint={`${product.category} product`}
                      height="64"
                      src={product.imageUrl}
                      width="64"
                    />
                  </TableCell>
                  <TableCell className="font-medium">{product.name}</TableCell>
                  <TableCell>{product.category}</TableCell>
                  <TableCell>{product.stock}</TableCell>
                  <TableCell>
                    <Badge variant={product.availability === 'Available' ? 'secondary' : 'outline'}>{product.availability}</Badge>
                  </TableCell>
                  <TableCell className="text-right">${product.dailyRate.toFixed(2)}</TableCell>
                  <TableCell>
                    <DropdownMenu>
                      <DropdownMenuTrigger asChild>
                        <Button aria-haspopup="true" size="icon" variant="ghost">
                          <MoreHorizontal className="h-4 w-4" />
                          <span className="sr-only">Toggle menu</span>
                        </Button>
                      </DropdownMenuTrigger>
                      <DropdownMenuContent align="end">
                        <DropdownMenuLabel>Actions</DropdownMenuLabel>
                        <DropdownMenuItem>Edit</DropdownMenuItem>
                        <DropdownMenuItem>View Calendar</DropdownMenuItem>
                        <DropdownMenuItem className="text-destructive">Delete</DropdownMenuItem>
                      </DropdownMenuContent>
                    </DropdownMenu>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </>
  );
}
